<template>
    <div class="canvas">
        <div class="print">
            <div class="print__page">
                <h2 class="print__title">     {{ order.order_id }}</h2>

                <p class="print__p print__p--date">
                    <span>. </span>
                    <span class="print__date">{{ date }}</span>
                </p>

                <p class="print__p">
                            <b></b>,               23 008878961,     <span class="print__renter">{{ renter }}</span>,    <b></b>,   ,      626-631  ,     :
                </p>

                <h3 class="print__caption">1.  </h3>
                <p class="print__p">
                    1.1.     (    ) : {{ products_name }}   ,    {{ products_cost }} .<br>
                    1.2.       : {{ order.start_time }}<br>
                    1.3.  : {{ deposit }}<br>
                    1.4.              .             .<br>
                </p>                

                <h3 class="print__caption">2.     </h3>
                <p class="print__p">
                    2.1.     :<br>
                    2.1.1.       ,   ,                . <br>
                    2.1.2.      .<br> 
                    2.1.3.                     ,        .<br> 
                    2.1.4.          .<br>
                    2.2.          ,     ,           ,          ,    2.1.  <br>
                    2.3.      ,      ,     ,        
                </p>

                <h3 class="print__caption">3.     </h3>
                <p class="print__p">
                        :<br>
                    3.1.           .<br> 
                    3.2.       ,   ,                .<br>
                    3.3.        ,  ,       ,        .<br>
                    3.4.        -              .<br>
                    3.5.            ,        .<br> 
                    3.6.       ,                  .6.  .   ,     .<br>
                    3.7.     ,       ,                   20%    
                </p>

                <h3 class="print__caption">4.   </h3>
                <p class="print__p">
                    4.1.      ,      ,   .<br>
                    4.2.             31   . <br>
                    4.3.     ,     ,      .<br>
                </p>
                
                <h3 class="print__caption">5.     </h3>
                <p class="print__p">
                    5.1.     152 ( .  29.07.2017) "  "                      .<br>
                    5.2  . 5 . 6    152 ( .  29.07.2017) "  "          <br>

                </p>

                <h3 class="print__caption">6.       </h3>
                <p class="print__p">
                    <table class="print__p--info">
                        <tr>
                            <td>
                                :    <br>
                                : 230214171784<br>
                                : 314237201600076<br>
                                : ., ., .222<br>
                                : +7 (918) 941-34-34, +7 (903) 410-44-94
                            </td>
                            <td>
                                : {{ renter }}<br>
                                : {{ passport }}<br>
                                 : {{ address }}<br>
                                : {{ phone }}
                            </td>
                        </tr>                        
                        <tr>
                            <td>
                                <br>
                                _______________/   ../<br>
                                {{ date }}    
                            </td>
                            <td>
                                <br>
                                ___________ /{{ renter_small}} /<br>
                                {{ date }}                                
                            </td>
                        </tr>
                    </table>
                </p>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            order: Object,
        },

        mounted() {
            this.print()
        },

        methods: {
            close() {
                this.$emit('close')
            },

            print() {
                window.print()
                this.close()
            },
        },

        computed: {
            date() {
                const time = this.order.start_time

                const year = time.slice(0,4)
                const month = time.slice(5,7)
                const day = time.slice(8,10)

                return `${day}.${month}.${year} .`
            },

            renter() {
                return this.order.customer_name 
                    ? this.order.customer_name 
                    : '_______________________'
            },
            passport() {
                return this.customer ? this.customer.passport : ''
            },

            address() {
                return this.customer ? this.customer.address : ''
            },
            phone() {
                return this.customer ? this.customer.phone : ''
            },


            renter_small() {
                if (!this.customer) {
                    return '_______________________'
                }

                const fname = this.customer.fname
                const sname = this.customer.sname ? this.customer.sname[0] + '.' : ''
                const tname = this.customer.tname ? this.customer.tname[0] + '.' : ''

                return `${fname} ${sname} ${tname}`
            },

            products() {
                const subOrders = this.$store.getters.orderProducts.filter(i => i.order_id == this.order.order_id)
                const products = subOrders.map(i => this.$store.getters.products.find(j => j.id_rent == i.product_id))
                
                return products
            },
            products_name() {
                return this.products.reduce((acc, item) => {
                    acc += `${item.name} (${this.products_cost} .), `

                    return acc
                }, '')
            },
            products_cost() {
                return this.products.reduce((acc, item) => {
                    acc += +item.cost

                    return acc
                }, 0)
            },
            customer() {
                return this.$store.getters.customers.find(i => i.id_rent == this.order.customer_id)
            },
            deposit () {                
                const deposit = this.$store.getters.deposits.find(i => i.id_rent == this.order.deposit)
                return deposit ? deposit.name : '______________'
            }
        }
    }
</script>

<style>
    @page {
        size:  auto;   /* auto is the initial value */
        margin: 0mm;  /* this affects the margin in the printer settings */
    }

    p {
        margin: 0;
    }
    .canvas {
        overflow: scroll;
    }

    .print {
        font-family: TimesNewRoman;
        font-size: 12px;
    }
    .print__page {
        width: 210mm;
        height: 297mm;
        box-sizing: border-box;
        padding: 10mm 10mm 10mm 15mm;
        line-height: 1.2;

        background-color: #fff;
        color: #333;
    } 

    .print__title {
        margin: 10px 0 0 0;
        text-align: center;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 13px
    }
    .print__caption {
        margin: 15px 0 0 0;
        text-align: center;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 13px
    }
    .print__p {
        margin: 5px 0 0 0;
    }
    .print__p--date {
        display: flex;
        justify-content: space-between;
    }

    .print__p--info {
        width: 100%
    }
    .print__p--info td {
        width: 50%;
    }
</style>
